if (mq.matches) {
} else {
    !(function (e) {
        "undefined" == typeof module
            ? (this.charming = e)
            : (module.exports = e);
    })(function (e, n) {
        "use strict";
        n = n || {};
        var t = n.tagName || "span",
            o = null != n.classPrefix ? n.classPrefix : "char",
            r = 1,
            a = function (e) {
                for (
                    var n = e.parentNode, a = e.nodeValue, c = a.length, l = -1;
                    ++l < c;

                ) {
                    var d = document.createElement(t);
                    o && ((d.className = o + r), r++),
                        d.appendChild(document.createTextNode(a[l])),
                        n.insertBefore(d, e);
                }
                n.removeChild(e);
            };
        return (
            (function c(e) {
                for (
                    var n = [].slice.call(e.childNodes), t = n.length, o = -1;
                    ++o < t;

                )
                    c(n[o]);
                e.nodeType === Node.TEXT_NODE && a(e);
            })(e),
            e
        );
    });

    class HoverImgFx2 {
        constructor(el) {
            this.DOM = { el: el };
            this.DOM.reveal = document.createElement("div");
            this.DOM.reveal.className = "hover-reveal";
            this.DOM.reveal.innerHTML = `<div class="hover-reveal__inner"><div class="hover-reveal__img" style="background-image:url(${this.DOM.el.dataset.img})"></div></div>`;
            this.DOM.el.appendChild(this.DOM.reveal);
            this.DOM.revealInner = this.DOM.reveal.querySelector(
                ".hover-reveal__inner"
            );
            this.DOM.revealInner.style.overflow = "hidden";
            this.DOM.revealImg =
                this.DOM.revealInner.querySelector(".hover-reveal__img");

            this.initEvents();
        }

        initEvents() {
            this.positionElement = (ev) => {
                const mousePos = getMousePos(ev);
                const docScrolls = {
                    left:
                        document.body.scrollLeft +
                        document.documentElement.scrollLeft,
                    top:
                        document.body.scrollTop +
                        document.documentElement.scrollTop,
                };
                this.DOM.reveal.style.top = `${
                    mousePos.y + 20 - docScrolls.top
                }px`;
                this.DOM.reveal.style.left = `${
                    mousePos.x + 20 - docScrolls.left
                }px`;
            };

            this.mouseenterFn = (ev) => {
                this.positionElement(ev);
                this.showImage();
            };
            this.mousemoveFn = (ev) =>
                requestAnimationFrame(() => {
                    this.positionElement(ev);
                });
            this.mouseleaveFn = () => {
                this.hideImage();
            };

            this.DOM.el.addEventListener("mouseenter", this.mouseenterFn);
            this.DOM.el.addEventListener("mousemove", this.mousemoveFn);
            this.DOM.el.addEventListener("mouseleave", this.mouseleaveFn);
        }

        showImage() {
            gsap.killTweensOf(this.DOM.revealInner);
            gsap.killTweensOf(this.DOM.revealImg);

            this.tl = gsap
                .timeline({
                    onStart: () => {
                        this.DOM.reveal.style.opacity = 1;
                        gsap.set(this.DOM.el, { zIndex: 1000 });
                    },
                })
                .add(() =>
                    gsap.fromTo(
                        this.DOM.revealInner,
                        { x: "-100%", y: "-100%" },
                        { x: "0%", y: "0%", duration: 0.4, ease: "power3.out" }
                    )
                )
                .add(() =>
                    gsap.fromTo(
                        this.DOM.revealImg,
                        { x: "100%", y: "100%" },
                        { x: "0%", y: "0%", duration: 0.4, ease: "power3.out" }
                    )
                );
        }

        hideImage() {
            gsap.killTweensOf(this.DOM.revealInner);
            gsap.killTweensOf(this.DOM.revealImg);

            this.tl = gsap
                .timeline({
                    onStart: () => {
                        gsap.set(this.DOM.el, { zIndex: 999 });
                    },
                    onComplete: () => {
                        gsap.set(this.DOM.el, { zIndex: "" });
                        gsap.set(this.DOM.reveal, { opacity: 0 });
                    },
                })
                .add(() =>
                    gsap.to(this.DOM.revealInner, {
                        x: "100%",
                        y: "100%",
                        duration: 0.3,
                        ease: "power3.out",
                    })
                )
                .add(() =>
                    gsap.to(this.DOM.revealImg, {
                        x: "-100%",
                        y: "-100%",
                        duration: 0.3,
                        ease: "power3.out",
                    })
                );
        }
    }

    [...document.querySelectorAll('[data-fx="2"] > a, a[data-fx="2"]')].forEach(
        (link) => new HoverImgFx2(link)
    );

    // Demo purpose only: Preload all the images in the page..
    const contentel = document.querySelector(".preview");
    [
        ...document.querySelectorAll(
            ".block__title, .block__link, .preview__text-link"
        ),
    ].forEach((el) => {
        const imgsArr = el.dataset.img.split(",");
        for (let i = 0, len = imgsArr.length; i <= len - 1; ++i) {
            const imgel = document.createElement("img");
            imgel.style.visibility = "hidden";
            imgel.style.width = 0;
            imgel.src = imgsArr[i];
            imgel.className = "preload";
            // contentel.appendChild(imgel);
        }
    });
    imagesLoaded(document.querySelectorAll(".preload"), () =>
        document.body.classList.remove("loading")
    );
}
